<template>
  <div class="users">
      <!-- 面包屑导航区域 -->
      <el-breadcrumb separator-class="el-icon-arrow-right">
            <el-breadcrumb-item :to="{ path: '/Home' }">首页</el-breadcrumb-item>
            <el-breadcrumb-item>用户管理</el-breadcrumb-item>
            <el-breadcrumb-item>用户列表</el-breadcrumb-item>
     </el-breadcrumb>

      <!-- 卡片视图区域   -->
    <el-card >
       <!-- 搜索与添加区域 -->
        <el-row :gutter="20">
            <el-col :span="9">
                 <el-input placeholder="请输入内容" v-model="queryInfo.query" clearable @clear="getUserList"> 
                    <el-button slot="append" icon="el-icon-search" @click="getUserList"></el-button>
                </el-input>
            </el-col>
            <el-col :span="4">
                <el-button type="primary" @click="addDialogVisible=true">添加用户</el-button>
            </el-col>
        </el-row>

        <!-- 用户列表区域 -->
        <el-table :data="userList" border stripe>
            <el-table-column type="index"></el-table-column>
            <el-table-column label="姓名" prop="username"></el-table-column>
            <el-table-column label="邮箱" prop="email"></el-table-column>
            <el-table-column label="电话" prop="mobile"></el-table-column>
            <el-table-column label="角色" prop="role_name"></el-table-column>
             <el-table-column label="状态">
                 <template slot-scope="scope">
                    <!-- {{scope.row}} -->
                    <el-switch v-model="scope.row.mg_state" @change="userStateChanged(scope.row)"></el-switch>
                 </template>
             </el-table-column>
            <el-table-column label="操作" width="190">
                <template slot-scope="scope">
                        <!-- 修改按钮 -->
                        <el-button type="primary" icon="el-icon-edit" size="mini" @click="showEditDialog(scope.row.id)"></el-button>
                        <!-- 删除按钮 -->
                        <el-button type="danger" icon="el-icon-delete" size="mini" @click="removeUserByid(scope.row.id)"></el-button>
                        <!-- 分配角色按钮 -->
                         <el-tooltip effect="dark" content="分配角色" placement="top" :enterable="false">
                            <el-button type="warning" icon="el-icon-setting" size="mini" @click="ss(scope.row)"></el-button>
                        </el-tooltip>
                       
                </template>
            </el-table-column>
            
        </el-table>

        <!-- 分页功能 -->
         <el-pagination
            @size-change="handleSizeChange"
            @current-change="handleCurrentChange"
            :current-page="queryInfo.pagenum"
            :page-sizes="[1,2,5,10]"
            :page-size="queryInfo.pagesize"
            :total="total"
            layout="total,sizes,prev,pager,next,jumper">
        </el-pagination>
    </el-card>

    <!-- 添加用户的弹框 -->
    <el-dialog
        title="添加用户"
        :visible.sync="addDialogVisible "
        width="50%"
        @close="addDialogClosed"
        >
        <!-- 内容主题区域 -->
        <el-form :model="addForm" :rules="addFormRules" ref="addFormRef" label-width="70px" class="demo-ruleForm">
            <el-form-item label="用户名 " prop="username">
                <el-input v-model="addForm.username"></el-input>
            </el-form-item>
             <el-form-item label="密码 " prop="password">
                <el-input v-model="addForm.password"></el-input>
            </el-form-item>
             <el-form-item label="邮箱 " prop="email">
                <el-input v-model="addForm.email"></el-input>
            </el-form-item>
             <el-form-item label="手机 " prop="mobile">
                <el-input v-model="addForm.mobile"></el-input>
            </el-form-item>
        </el-form>
        <!-- 底部区域 -->
        <span slot="footer" class="dialog-footer">
            <el-button @click="addDialogVisible = false">取 消</el-button>
            <el-button type="primary" @click="addUser">确 定</el-button>
        </span>
    </el-dialog>
    <!-- 修改用户对话框 -->
    <el-dialog
  title="修改用户"
  :visible.sync="editDialogVisidle"
  width="50%"
  @close="editDialogClosed"
 >
<el-form :model="editForm" :rules="editFormRules " ref="editFormRef" label-width="70px" >
  <el-form-item label="用户名" >
    <el-input v-model="editForm.username" disabled></el-input>
  </el-form-item>
    <el-form-item label="邮箱"  prop="email">
    <el-input v-model="editForm.email" ></el-input>
  </el-form-item>
    <el-form-item  label="用户名" prop="mobile" >
    <el-input v-model="editForm.mobile" ></el-input>
  </el-form-item>
  </el-form>
  <span slot="footer" class="dialog-footer">
    <el-button @click="editDialogVisidle = false">取 消</el-button>
    <el-button type="primary" @click="editUserinfo">确 定</el-button>
  </span>
</el-dialog>

<!-- 分配角色 -->
<el-dialog
  title="分配角色"
  :visible.sync="setRoleDialogVisible"
  width="50%"
>
    <div>
        <p>当前的用户{{userInfo.username}}</p>
        <p>当前的角色{{userInfo.role_name}}</p>
        <p>
            分配新角色  
            <el-select v-model="selectedRouleid" placeholder="请选择">
                <el-option
      v-for="item in roleslist"
      :key="item.id"
      :label="item.roleName"
      :value="item.id">
    </el-option>
  </el-select>
        </p>
    </div>
  <span slot="footer" class="dialog-footer">
    <el-button @click="setRoleDialogVisible = false">取 消</el-button>
    <el-button type="primary" @click="gengai">确 定</el-button>
  </span>
</el-dialog>
  </div>
</template>

<script>
export default {
name:"",
  components: {},
  props: {},
  data() {
      //验证邮箱规则
      var checkEmail = (rule,value,cb)=>{
          const regEmail = /^([a-zA-Z0-9_-])+@([a-zA-Z0-9_-])+(\.[a-zA-Z0-9_-])+/
            if(regEmail.test(value)){
                return cb()
            }
            cb(new Error("请输入合法的邮箱"))

      }

 
    //验证手机号规则
      var checkMobile = (rule,value,cb)=>{
          const regMobile=/^(0|86|17951)?(13[0-9]|15[0123456789]|17[678]|18[0-9]|14[57])[0-9]{8}$/
          if(regMobile.test(value)){
              return cb()
          }
          cb(new Error("请输入合法的手机号"))
      }
    return {
        //获取用户列表的参数对象
        queryInfo:{
            //获取用户列表数据
            query:'',
            // 当前页数
            pagenum:1,
            // 每页显示两条数据
            pagesize:2
        },
        setRoleDialogVisible:false,
        userList:[],
        roleslist:[],
        //修改
        editDialogVisidle:false,
        editForm:{},
        userInfo:{},
selectedRouleid:"",
        editFormRules:{
        email:[
     {required:true,message:'请输入用户邮箱',trigger:'blur'},
     {
         validator:checkEmail,trigger:"blur"
     }
     
     ],
     mobile:[
    {required:true,message:'请输入用户手机',trigger:'blur'},
     {
         validator:checkMobile,trigger:"blur"
     },
]
        },
        total:0,
        //控制添加用户弹框的显示与隐藏 
        addDialogVisible:false,
        // 添加用户的表单数据
        addForm:{
            username:'',
            password:'',
            email:'',
            mobile:''
        },
        //添加用户表单的验证规则对象
        addFormRules:{
            username:[
                {required:true,message:'请输入用户名',trigger:'blur'},
                {min:3,max:10,message:'用户名的长度在3~10个字符之间',trigger:'blur'}
            ],
            password:[
                {required:true,message:'请输入密码',trigger:'blur'},
                {min:6,max:15,message:'用户名的长度在6~15个字符之间',trigger:'blur'}
            ],
             email:[
                {required:true,message:'请输入密码',trigger:'blur'},{validator:checkEmail,trigger:"blur"}
            ],
            mobile:[
                {required:true,message:'请输入手机号',trigger:'blur'},{validator:checkMobile,trigger:"blur"}
            ]
        }
    };
  },
  watch: {},
  computed: {},
  methods: {
       //获取用户列表数据
     async getUserList(){
          let {data:res} = await this.$http.get('users',
          {params:this.queryInfo})
          console.log(res)
          if(res.meta.status !==200){
              return this.$message.error("获取不到用户列表数据")
          }else{
              this.userList=res.data.users
              this.total = res.data.total
          }
      },
        //监听用户框的的关闭事件
      addDialogClosed(){
          this.$refs.addFormRef.resetFields()
      },
      //监听页数改变的事件
      handleSizeChange(newSize){
        //   console.log(newSize)
          this.queryInfo.pagesize=newSize
          this.getUserList();
      },
      //监听页码值改变的事件
      handleCurrentChange(newPage){
          this.queryInfo.pagenum=newPage
          this.getUserList();
        //   console.log(newPage)
      },
      //监听switch状态的改变
      async userStateChanged(userinfo){
          let {data:res} = await this.$http.put(`users/${userinfo.id}/state/${userinfo.mg_state}`)
          console.log(res)
          if(res.meta.status !==200){
              userinfo.mg_state=!userinfo.mg_state
              return this.$message.error('更新用户状态失败')
          }else{
              this.$message.success("更新用户状态成功!")
          }
      },
      addUser(){
          this.$refs.addFormRef.validate(async valid=>{
              console.log(valid)
               if(!valid) return
               //可以发送请求
            const {data:res}= await this.$http.post("users",this.addForm)
            if(res.meta.status !==201){
                this.$message.error("添加用户失败")

            }
            this.$message.success("添加用户成功")
            this.addDialogVisible=false
            this.getUserList()
          })
      },
      //展示编辑
         async showEditDialog(e){
          const {data:res} =await this.$http.get('users/'+e)
          if(res.meta.status !==200){ 
              return this.$message.error("查询用户失败！") 
          }
           this.editForm=res.data;
          this.editDialogVisidle=true
         
    console.log(editForm)
      },
     async ss(userInfo){
         
          this.userInfo=userInfo
          const {data:res}= await this.$http.get("roles")
          if(res.meta.status !==200){
              return this.$message.error("获取角色列表失败")
          }
          this.roleslist=res.data
              this.setRoleDialogVisible=true
          console.log(  this.roleslist)
    
      },
      //
      editDialogClosed(){
            this.$refs.editFormRef.resetFields()
      },
      
      editUserinfo(){
              this.$refs.editFormRef.validate(async valid=>{
              console.log(valid) 
                  if(!valid) return 
                     //可以发送请求
            const {data:res}= await this.$http.put("users/"+this.editForm.id,{
                email:this.editForm.email,
                mobile:this.editForm.mobile
            })
            if(res.meta.status !==200){
                return this.$message.error("更新用户失败")

            }
            //关闭对话框
            this.editDialogVisidle=false
            //刷新
            this.getUserList()
            this.$message.success("更新用户成功")

              })
           
           
      },
     async removeUserByid(id){
       const res= await this.$confirm('此操作将永久删除该用户, 是否继续?', '提示', {
          confirmButtonText: '确定',
          cancelButtonText: '取消',
          type: 'warning'
        }).catch(err=>err)
        // console.log(res)
        if(res !=="confirm"){
            return this.$message.info("已取消删除")
        }
        const {data:res1}=await this.$http.delete("users/"+id)
        if(res1.meta.status !==200){
            return this.$message.error("删除用户失败")
        }
        this.$message.success("删除用户成功")
               this.getUserList()

      },
     async gengai(){
          if(!this.selectedRouleid){
              return this.$message.error("请选择分配的角色")
          }
        const {data:res}= await this.$http.put(`users/${this.userInfo.id}/role`,{
              rid:this.selectedRouleid
          })

          if(res.meta.status !==200){
              return this.$message.error("更新角色失败")
          }
          this.$message.success("更新角色成功")
          this.getUserList()
          this.setRoleDialogVisible=false
      }
  },
  created() {
      //获取用户列表数据
      this.getUserList()
  },
  mounted() {
     
  }
};
</script>
<style lang="scss" scoped>
.users{
    width: 100%;
    height: 100%;
    .el-table{
        margin-top: 15px;
    }
}
</style>